import { Component, OnInit } from '@angular/core';
import {SelectedpropertydetailService} from './selectedpropertydetail.service'


@Component({
  selector: 'app-selectedpropertydetail',
  templateUrl: './selectedpropertydetail.component.html',
  styleUrls: ['./selectedpropertydetail.component.css']
})
export class SelectedpropertydetailComponent implements OnInit {
  user: any;
  uid: any;
  return: any;
  isLoggedIn: boolean;
  isLoading: boolean = false;
  propertyDetails = [];
  constructor(private  SelectedpropertydetailService:  SelectedpropertydetailService) { }

  ngOnInit() {
    
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;

    // Fetch details
    this.SelectedpropertydetailService.getSellerProperties(this.uid).then(res => {
      res.forEach(element => {
        this.propertyDetails.push(element.data());
      });
      console.log(this.propertyDetails);
    });
  
  }

}
